import React, { Component } from 'react';
import Header from './components/Header';
import Shrtcode from './components/Shrtcode';
import Shrtcodeinfo from './components/Shrtcodeinfo';
import Characteristics from './components/Characteristics';
import Footer from './components/Footer';
import working from './img/illustration-working.svg';
import brand from './img/icon-brand-recognition.svg';
import detailed from './img/icon-detailed-records.svg';
import fully from './img/icon-fully-customizable.svg';
import './css/App.css';

class App extends Component {

  constructor() {
    super ()
    this.state={
    showMe:false
  }
  }

  state = {
       originallink: [],
       shortlink: [],
       error: null,
  };

  getShorten =async e =>{
        e.preventDefault();
        const {link} = e.target.elements;
        const linkvalue = link.value;

        const API_URL=`https://api.shrtco.de/v2/shorten?url=${linkvalue}`
        const response = await fetch(API_URL);
        const data = await response.json(); 

             
        this.setState({
         originallink: data.result.original_link,  
         shortlink: data.result.short_link,
         error: null,
         showMe:true
         });


  }


  render() {
   
    return (     
      <div className="Container">
        <Header/>
      <div className="information">
      <div className="information-texto">
      <h1>More than just shorter links</h1>
      <p>Build your grand's recognition and get detailed insights on how your link are performing.</p>
      <a href="#">Get Started</a>
      </div>  
      <img src={working} alt="ilustracion-working"/>
      </div>
      <div className="grey">
      <div className="Shortlink">
      <Shrtcode getShorten={this.getShorten}/>
      { 
       this.state.showMe?
      <div>
      <Shrtcodeinfo {...this.state}/>
      </div>
      :null
      }
      
      </div>
      
      <div className="texto">
         <h3>Advanced Statistics</h3>
         <p>Track how your links are performing across the web with <br/> our advanced Statistics dashboard</p>
     </div>

      <div className="Characteristics">
        <div  id="card-1">
        <Characteristics
          icon={brand}
          titulo="Brand Recognition"
          parrafo="Boost your brand recognition with each click. Generic links don't mean a thing. Branded links help instil confidence in your content."
        />
        </div>  
        <div className="rect-1"><svg><rect/></svg></div>
        <div id="card-2">
        <Characteristics
          icon={detailed}
          titulo="Detailed Records"
          parrafo="Gain insights into who is clicking your links. Knowing when and where people engage with your content helps inform better decisions"
        />
        </div>
        <div className="rect-2"><svg><rect/></svg></div>
        <div id="card-3">
        <Characteristics        
          icon={fully}
          titulo="Fully Customizable"
          parrafo="Improve brand awareness and content discoverability through customizable links, supercharging audience engagement."
        />
        </div>
        </div>
        </div>
        <Footer/>
      </div>
    );
  }
}

export default App;
